.feature {
    background-color: $lightGrayishBlue;
    text-align: center;


    @include breakpoint-up(medium){
        text-align: left;
    }
    
    &__intro {

        margin-bottom: 3.75rem; //60px/16px

        @include breakpoint-up(large) {
           width: 60%;
        
        }
    }
    &__grid {

        @include breakpoint-up(medium) {
            display: flex;
            flex-wrap: wrap;
        }
    }

    &__item {
        //border: 1px solid magenta;
        padding: 0.9375rem; //15px (adding up to 30px between each column)/16px 
        
        @include breakpoint-up(medium) {
            flex: 0 0 50%; //flex grow flex shrink flex basis
        }
        
        @include breakpoint-up(large) {
            flex: 1; //fits available width
        }
    }

    &__icon {
        margin-bottom: 1.875rem;

        @include breakpoint-up(large){
          margin-bottom: 2.75rem;
        }
    }

    &__title {
        font-size: 1.25rem;
        font-weight: 300;
        line-height: 1.15;
        color: $darkBlue;
        margin-bottom: 1.5rem; //24px/16px
    
        @include breakpoint-up(large){
          font-size: 1.5rem;
          margin-bottom: 1.5rem;
        }
    }

    &__description {
        font-size: 0.875rem; //14px/16px
        line-height: 1.5; 
    }
}