.header {
    position: relative;
    z-index: 1;  

          &.open {
        
            .header__toggle {

              > span:first-child {
              transform: rotate(45deg);
              }
              
              > span:nth-child(2) {
              opacity: 0;
              }
              
              > span:last-child {
              transform: rotate(-45deg);
              }
            } //header toggle
          }


    .overlay {
      //display: none; 
      opacity: 0;
      position: fixed;
      top: 0px;
      right: 0px;
      bottom: 0px;
      left: 0px;
      background-image: linear-gradient($darkBlue, transparent);
  }

  nav {
    position: relative;
    background-color: $white;
    padding-top: 1.0625rem;
    padding-bottom: 1.0625rem;
}

  &__logo {
      img {
        width: 8.8125rem;
        height: 1.375rem;
      }
    } 

     &__toggle { // Mobile Menu Toggle
       
      > span {
          display: block;
          width: 26px;
          height: 2px;
          background-color: $darkBlue;
          transition: all 300ms ease-in-out;
          transform-origin: 3px 1px;
         

          &:not(:last-child) {
              margin-bottom: 5px;
          }
        }
      }  //header toggle

    &__menu {
        background: $white;
        padding: 1.625rem;
        position: absolute;
        width: calc(100% - 3rem); //100% -1.5 + 1.5rem padding on each side
        /*postion the absolute element with the below
        as we cannot use margin: 0 auto */
        left: 50%;
        transform: translateX(-50%);

        margin-top: 1.5rem;
        border-radius: 5px;
    
        a {
            display: block;
            padding: 0.625rem;
            color: $darkBlue;
            text-align: center;
            }
      }

    &__links {

    a {
        position: relative;
        font-size: $font-sm;
        color: $grayishBlue;
        transition: color 300ms ease-in-out;
  
        &:not(:last-child){
          margin-right: 32px;
        }
  
        &::before {
          content: "";
          display: block;
          position: absolute;
          height: 5px;
          left: 0;
          right: 0;
          bottom: -30px;
          background: linear-gradient(to right, $limeGreen, $brightCyan);
          opacity: 0;
          transition: opacity 300ms ease-in-out;
        }
        &:hover {
          color: $darkBlue;
  
          &::before {
            opacity: 1; //transition the underline effect to visible
          }
        }
      }
    } //mobile menu
} 




